<template>
    <div class="table">
        <div class="container">
            <el-form ref="filtersForm" :inline="true" :model="filters" class="toolbar">
                <el-form-item label="销售单号" :inline="true">
                    <el-input v-model.trim="filters.movieID" placeholder="销售单号"></el-input>
                </el-form-item>
                <el-form-item label="客户名称" :inline="true">
                    <el-input v-model.trim="filters.movieName" placeholder="客户名称"></el-input>
                </el-form-item>
                <el-form-item label="订单状态">
                    <el-select v-model="filters.value" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-button>查询</el-button>
            </el-form>
            <el-table :data="data" border class="table" ref="multipleTable" @selection-change="handleSelectionChange">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="name" label="销售单号" width="120">
                </el-table-column>
                <el-table-column prop="name" label="车辆数" width="120">
                </el-table-column>
                <el-table-column prop="name" label="金额" width="120">
                </el-table-column>
                <el-table-column prop="name" label="车主名称" width="120">
                </el-table-column>
                <el-table-column prop="name" label="订单状态" width="120">
                </el-table-column>
                <el-table-column prop="name" label="当前处理人" width="120">
                </el-table-column>
                <el-table-column prop="name" label="创建日期" width="120">
                </el-table-column>
                <el-table-column prop="name" label="创建人" width="120">
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="scope">
                        <el-button type="text" icon="el-icon-view" @click="">查看</el-button>
                        <el-button type="text" icon="el-icon-edit" @click="eventEdit(scope.$index, scope.row)">审核</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="1000">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    import { tableApi, tableSearch } from '@/api/table'
    export default {
        name: 'basetable',
        data() {
            return {
                tableData: [],
                curPage: 1,
                multipleSelection: [],
                selectCate: '',
                selectWord: '',
                delList: [],
                isSearch: true,
                filtersForm: {
                    name: '',
                    date: '',
                    address: ''
                },
                formRefund: {
                    name: '',
                    date: '',
                    address: ''
                },
                filters: {
                    movieID:'',
                    movieName:'',
                    value:'3'
                },
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }],
                idx: -1
            }
        },
        created() {
            this.getData();
        },
        computed: {
            data() {
                return this.tableData.filter((d) => {
                    let is_del = false;
                    for (let i = 0; i < this.delList.length; i++) {
                        if (d.name === this.delList[i].name) {
                            is_del = true;
                            break;
                        }
                    }
                    if (!is_del) {
                        if (d.address.indexOf(this.selectCate) > -1 &&
                            (d.name.indexOf(this.selectWord) > -1 ||
                                d.address.indexOf(this.selectWord) > -1)
                        ) {
                            return d;
                        }
                    }
                })
            }
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.curPage = val;
                this.getData();
            },
            // 获取 easy-mock 的模拟数据
            getData() {
                // 开发环境使用 easy-mock 数据，正式环境使用 json 文件
                /*if (process.env.NODE_ENV === 'development') {
                    this.url = '/mock/table';
                };*/
                tableApi({page: this.curPage}).then(res => {
                    this.tableData = res.data.list;
                })
                
                /*this.$axios.get(this.url, {
                    page: this.curPage
                }).then((res) => {
                    this.tableData = res.data.data.list;
                })*/
            },
            search() {
                this.isSearch = true;
                tableSearch({name: 'sadasd', age: '4242'}).then(res => {
                    console.log(res)
                })
            },
            formatter(row, column) {
                return row.address;
            },
            filterTag(value, row) {
                return row.tag === value;
            },
            eventEdit(index, row) {
                this.idx = index;
                const item = this.tableData[index];
                this.formRefund = {
                    name: item.name,
                    date: item.date,
                    address: item.address
                }
            },
            delAll() {
                const length = this.multipleSelection.length;
                let str = '';
                this.delList = this.delList.concat(this.multipleSelection);
                for (let i = 0; i < length; i++) {
                    str += this.multipleSelection[i].name + ' ';
                }
                this.$message.error('删除了' + str);
                this.multipleSelection = [];
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
</style>
